import React from "react";
import produtos from "../../../data/produtos";

export default props => {
        
    const listasProdutosJSX = produtos.map((produto) => {
        return (
        <li key = {produto.id}> 
        {produto.id}) - {produto.nome} - R$ {produto.preço}
    </li>
        );
  });  
  
    return (
        <div>
            <ul style={{ listStyle: 'none'}}>
                { listasProdutosJSX }
            </ul>
        </div>
    );
};