import React from "react";
import '../../../components/basicos/repeticao/TabelaProdutos'
import '././../repeticao/TabelaProdutos2.css'

import produtos2 from "../../../data/produtos2";

export default (props) => {
        
    function getLinhas() {
        return produtos2.map((produto2, i) => {
            return (
                <tr key={produto2.id} className={ i % 2 === 0 ? 'Par' : 'Impar'}>
                    <td>{produto2.id})-</td>
                    <td>{produto2.nome}</td>
                    <td>R${produto2.preco.toFixed(2).replace('.', ',')}</td>
                </tr>
            )
        })
    }

    return (
        <div className="desafioProdutos2">
            <table>
             <thead>
                <tr>
                    <th>Id</th>
                    <th>Nome</th>
                    <th>Preço</th>
                </tr>
            </thead>
            <tbody>
                {getLinhas()}
            </tbody>
            </table>    
        </div>
    )
};