import React from "react";
import alunos from "../../../data/alunos";

export default props => {
        
    const listasAlunosJSX = alunos.map((aluno) => {
        return (
        <li key = {aluno.id}> 
        { aluno.id })  - {aluno.nome} - { aluno.nota}
    </li>
        );
  });  
  
    return (
        <div>
            <ul style={{ listStyle: 'none'}}>
                { listasAlunosJSX }
            </ul>
        </div>
    );
};