import "./App.css";
import React from "react";


import IndiretaPai from "./components/comunicacao/IndiretaPai";
import DiretaPai from "./components/comunicacao/DiretaPai";
import UsuarioInfo from "./components/condicional/UsuarioInfo";
import ParOuImpar from "./components/condicional/ParOuImpar";
import TabelaProdutos2 from "./components/basicos/repeticao/TabelaProdutos2";
import TabelaProdutos from "./components/basicos/repeticao/TabelaProdutos";
import ListaAlunos from "./components/basicos/repeticao/ListaAlunos";
import FamiliaMembro from "./components/basicos/FamiliaMembro"
import Familia from "./components/basicos/Familia"
import PrimeiroComponente from './components/basicos/PrimeiroComponente'
import SegundoComponente from './components/basicos/SegundoComponente'
import ComponenteComParametro from './components/basicos/ComponenteComParametro'
import ComponenteParametrizado from './components/basicos/ComponenteParametrizado'
import Fragmento from './components/basicos/Fragmento'
import Aleatorio from "./components/basicos/Aleatorio";
import Card from "./components/layout/Card";


export default (props) => {
    return (
        <div className="App">

            <h1>Fundamentos React J.S</h1>

            <div className="Cards">

            <Card titulo ="#12 - Comunicação Indireta" color="#8BAD39">
                 
                    <IndiretaPai></IndiretaPai>
                      
                          
                 </Card>

            <Card titulo ="#11 - Comunicação Direta" color="#59323C">
                 
            <DiretaPai></DiretaPai> 
                 
                     
            </Card>

            <Card titulo ="#10 - Renderização Condicional" color="#982395">
                 
                 <ParOuImpar numero={20}>
                 </ParOuImpar>     

                 <UsuarioInfo usuario={{ nome: 'Eduardo' }}>  </UsuarioInfo>        
                 
                     
            </Card>

            <Card titulo ="#09 - Array de Produtos com .Map - 2" color="#3A9AD9">
                 
                 <TabelaProdutos2>

                 </TabelaProdutos2>               
                     
            </Card>

            <Card titulo ="#08 - Array de Produtos com .Map" color="#FF4C65">
                 
                 <TabelaProdutos>
                     

                 </TabelaProdutos>
                     
            </Card>

            <Card titulo ="#08 - Repetição - Alunos com .Map" color="#FF4C65">
                 
                 <ListaAlunos>                  

                 </ListaAlunos>
                     
            </Card>
            
            <Card titulo ="#07 - Componentes com Childrens" color="#00C8F8">
                 <Familia sobrenome="Olimpio">
                     <FamiliaMembro nome="Roberto " />
                      
                     <FamiliaMembro nome="Roberto Eduardo " />
                     <FamiliaMembro nome="Fátima " />
                     <FamiliaMembro nome="Eduardo " />
                 </Familia>    
            </Card>

            <Card titulo ="#06 - Desafio Aleatório" color="#FA6900">
                 <Aleatorio min={1} max={60} />
            </Card>

            <Card titulo ="#05 - Fragmento" color="#E94C6F">
                <Fragmento />
            </Card>

            <Card titulo ="#04 - Com Parâmetro" color="#E8B71A">
                <ComponenteParametrizado
                endereco="Avenida Gil Veloso, Vila Velha"
                complemento="casa" />
            </Card>

            <Card titulo ="#03 - Com Parâmetro" color="#FA6900">
                <ComponenteComParametro
                titulo="Situação do Aluno"
                aluno="Eduardo Olimpio"
                nota={9.7}
                faltas={0} />
            </Card>

            
            <Card titulo ="#02 - Com Parâmetro" color="#E94C6F">
                <SegundoComponente />
            </Card>
            
            
            <Card titulo ="#01 - Com Parâmetro" color="#588C73">
                <PrimeiroComponente />
            </Card>

            </div>



        </div>
    )
}