import React from "react"

export default function ComParametro (props) {
     const status = props.nota >= 7 ? 'Aprovado' : 'Reprovado'
        return (
        <div>            
            <h2>{ props.titulo }</h2>
            <p> <strong>{ props.aluno }</strong> tem nota: { props.nota } suas faltas são:  {props.faltas} </p>
            <p>O aluno {props.aluno} está: <strong> { status }  </strong></p>
        </div>
    ) 
}