import React from 'react'
import "./botao.css"


export default props => {

    const min = 50
    const max = 70
    const gerarIdade = () => parseInt(Math.random() * ( max - min )) + min
    
    return (
        <div>
            <div>
                Filho
            </div>
            <button onClick= {
                function (e) {
                props.quandoClicar('Componente Pai', gerarIdade(), true)}} className='b-filho'>Fornecer Informações</button>
        </div>
    )
}