import React, { cloneElement } from "react";


export default props => {
    console.log(typeof props.children)
    return (
        <div>
           
           {
               props.children.map((filhos, i) => {
                   return cloneElement(filhos, { ...props, key:i })
               })
           }
           
        </div>
    )
}
// Operador Spread ...props faz com que pegue todos so elementos de uma vez..nesse caso o sobrenome.

